import { makeStyles } from '@material-ui/core/styles';

export default makeStyles((theme) => ({
    root: {

    },
    eventCard: {
        borderRadius: '0',
        display: 'flex',
        flexDirection: 'row'
    },
    cardActions: {
        marginLeft: 'auto',
    },
    editMenu: {
        width: '100%',
        height: '100vh',
        position: 'absolute',
        top: 0,
        left: 0,
        display: 'flex'
    },
    toolbar: theme.mixins.toolbar,
    editContent: {
        flexGrow: 1,
        padding: theme.spacing(3),
        position: 'relative',
        display: 'flex',
        flexDirection: 'column'
    },
    editTitle: {
        display: 'flex',
    },
    editID: {
        marginLeft: 'auto'
    },
    inputField: {
        minWidth: '50%',
    },
    fileInput: {
        width: '97%',
        margin: '10px 0',
    },
    editForm: {
        marginTop: '1rem'
    },
    partData: {
        marginTop: '1rem'
    },
    content: {
        display: 'inline-flex',
        justifyContent: 'space-between',
        width: '100%'
    },
    contentData: {
        textAlign: 'center'
    }
}));