import axios from 'axios'

const user_url = 'https://vilkkoni-server.herokuapp.com/user';

export const login = (key) => axios.post(`${user_url}/login`, key)
export const fetchAPIEvents = (key) => axios.get(`${user_url}/${key}/events`);
export const createEvent = (key, newEvent) => axios.post(`${user_url}/${key}/events`, newEvent);
export const updateEvent = (key, id, updatedEvent) => axios.patch(`${user_url}/${key}/events/${id}`, updatedEvent);
export const deleteEvent = (key, id) => axios.delete(`${user_url}/${key}/events/${id}`);
export const deleteParticipant = (key, id, parts) => axios.patch(`${user_url}/${key}/events/${id}/signups`, parts);

export const postNews = (key, data) => axios.post(`${user_url}/${key}/news`, data) 
export const getNews = (key) => axios.get(`${user_url}/${key}/news`) 