import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getAPIEvents, updateEvent, deleteEvent, deleteParticipant } from '../../../actions/events';
import * as dayjs from 'dayjs';

import { Typography, Card, CardContent, Grid, CardActions, Button, Paper, Slide, TextField, FormControlLabel, Checkbox, Dialog, DialogActions, DialogTitle, DialogContent, Divider } from '@material-ui/core';
import { DataGrid, GridToolbar } from '@material-ui/data-grid';
import FileBase from 'react-file-base64';
import { DateTimePicker } from '@material-ui/pickers';

import useStyles from './styles.js';
import { ArrowBackIos, Delete, Edit, PeopleAlt, Visibility } from '@material-ui/icons';

const EditMenu = ({api_key, ID, open, cancel}) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const event = useSelector((state) => ID ? state.events.find((p) => p._id === ID) : null);
    const [eventData, setEventData] = useState({creator: "Vilkkoni", title: "", message: "", location: "", tags: [""], image: "", price: 0, maxPartCount: 3, partCount: 0, beginDate: dayjs().toDate(), lastModified: dayjs().toDate()});
    const [notify, setNotify] = useState(true);

    useEffect(() => {
        if(event) setEventData(event);
    }, [event]);

    const saveEdit = (e) => {
        e.preventDefault();
        
        if (ID) dispatch(updateEvent(api_key, ID, eventData));
        cancel();
    }

    return (
        <Slide direction="left" in={open} className={classes.editMenu} mountOnEnter unmountOnExit>
            <Paper>
                <div className={classes.editContent}>
                <div className={classes.toolbar} />
                    <Grid container spacing={3}>
                        <Grid item><Button variant="contained" onClick={cancel}><ArrowBackIos/>Back</Button></Grid>
                        <Grid item><Typography variant="h4">Edit Event</Typography></Grid>
                        <Grid item className={classes.editID}><Typography variant="body2">(ID: {ID})</Typography></Grid>
                    </Grid>
                    <form className={classes.editForm} onSubmit={saveEdit}>
                        <Grid container direction="column" spacing={3}>
                            <Grid item><TextField className={classes.inputField} variant="outlined" label="Title" value={eventData.title} onChange={(d) => setEventData({...eventData, title: d.target.value })} /></Grid>
                            <Grid item><TextField className={classes.inputField} variant="outlined" label="Content" multiline rows={4} inputProps={{style: {resize: "both"}}} value={eventData.message} onChange={(d) => setEventData({...eventData, message: d.target.value })} /></Grid>
                            <Grid item><TextField className={classes.inputField} variant="outlined" label="Location" value={eventData.location} onChange={(d) => setEventData({...eventData, location: d.target.value})} /></Grid>
                            <Grid item><TextField className={classes.inputField} variant="outlined" label="Search tags, comma-delimited" value={eventData.tags} onChange={(d) => setEventData({...eventData, tags: d.target.value.split(',') })} /></Grid>
                            <Grid item container direction="row" alignItems="center" justify="flex-start">
                                <Grid item><TextField variant="outlined" label="Participant count" type="number" value={eventData.maxPartCount} style={{marginRight: '2rem'}} onChange={(d) => setEventData({...eventData, maxPartCount: d.target.value })} /></Grid>
                                <Grid item><DateTimePicker label="Date and time" value={eventData.beginDate} style={{marginRight: '2rem'}} onChange={(d) => setEventData({...eventData, beginDate: d })} ampm={false}/></Grid>
                                <Grid item><TextField variant="outlined" label="Ticket price" type="number" value={eventData.price} onChange={(d) => setEventData({...eventData, price: d.target.value })} /></Grid>
                            </Grid>
                            <Grid item><div className={classes.fileInput}><Typography>Select image for the event:</Typography><FileBase type="file" multiple={false} onDone={({base64}) => setEventData({...eventData, image: base64})} /></div></Grid>
                            <Grid item><FormControlLabel control={<Checkbox checked={notify} onChange={(s) => {setNotify(!notify)}} />} label="Notify subscribers about this change" /></Grid>
                            <Grid item container direction="row" alignItems="center" justify="flex-start">
                                <Grid item><Button variant="contained" color="primary" type="submit">Save</Button></Grid>
                            </Grid>
                        </Grid>
                    </form>
                </div>
            </Paper>
        </Slide>
    )
}

const part_columns = [
    {field: "name", headerName: "Name", flex: 1},
    {field: "email", headerName: "Email", flex: 1},
    {field: "phone", headerName: "Phone Number", flex: 1},
    {field: "count", headerName: "Count", flex: 1},
    {field: "additional", headerName: "Additional Information", flex: 1},
    {field: "subscribed", headerName: "Subscribed", flex: 1, type: 'boolean'},
];

const makeRows = (array) => {
    var count = 0;
    array.forEach(element => {
        element.id = count;
        count++;
    });
    return array;
}

const ViewParticipants = ({api_key, open, ID, cancel}) => {
    const classes = useStyles();
    const event = useSelector((state) => ID ? state.events.find((p) => p._id === ID) : null);
    const [selectionModel, setSelectionModel] = useState([]);
    const dispatch = useDispatch();

    const removeSelection = () => {
        if (selectionModel.length > 0) {
            const parts = event.participants.filter((part, idx) => selectionModel.includes(idx)).map((part) => part._id);
            dispatch(deleteParticipant(api_key, ID, parts));
            setSelectionModel([]);
        }
    }

    return (
        ID ? (
        <Slide direction="left" in={open} className={classes.editMenu} mountOnEnter unmountOnExit>
            <Paper>
                <div className={classes.editContent}>
                    <div className={classes.toolbar} />
                    <div style={{display: 'flex', justifyContent: 'space-between'}}>
                        <Button variant="contained" onClick={cancel}><ArrowBackIos/>Back</Button>
                        <Button variant="contained" color="secondary" onClick={removeSelection} disabled={!selectionModel.length}><Delete />Remove Selected</Button>
                    </div>
                    <DataGrid 
                        components={{
                            Toolbar: GridToolbar,
                        }}
                        className={classes.partData} 
                        columns={part_columns} 
                        rows={makeRows(event.participants.slice())}
                        checkboxSelection
                        disableSelectionOnClick
                        sortModel={[
                            {
                              field: 'name',
                              sort: 'asc',
                            },
                        ]}
                        onSelectionModelChange={(newSelection) => {
                            setSelectionModel(newSelection.selectionModel);
                        }}
                        selectionModel={selectionModel}
                        />
                </div>
            </Paper>
        </Slide>
        ) : ( null )
    )
}

const DeleteDialog = ({open, cancel, deleteEvent}) => {

    return (
        <Dialog open={open}>
            <DialogTitle>
                Cancel this event?
            </DialogTitle>
            <DialogContent>
                Are you sure you want to cancel this event? All participants will be notified of this action. You cannot recover this event after deletion.
            </DialogContent>
            <DialogActions style={{justifyContent: 'space-between'}}>
                <Button color="primary" variant="contained" onClick={cancel}>Go Back</Button>
                <Button color="secondary" variant="contained" onClick={deleteEvent}><Delete />Delete</Button>
            </DialogActions>
        </Dialog>
    )
}

const ManageEvents = ({ api_key }) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const [status, setStatus] = useState(400);
    const [currentID, setCurrentID] = useState(null);
    const [editing, setEditing] = useState(false);
    const [viewParts, setViewParts] = useState(false);
    const events = useSelector((state) => state.events);
    const [deleteOpen, setDeleteOpen] = useState(false);

    useEffect(() => {
        dispatch(getAPIEvents(api_key, call));
    }, [api_key, dispatch]);

    const call = (res) => {
        setStatus(res.status);
    }

    const cancelEdit = () => {
        setEditing(false);
        setCurrentID(null);
    }

    const cancelDelete = () => {
        setDeleteOpen(false);
        setCurrentID(null);
    }

    const cancelViewPart = () => {
        setViewParts(false);
        setCurrentID(null);
    }

    const confirmDelete = () => {

        dispatch(deleteEvent(api_key, currentID))

        setDeleteOpen(false);
        setCurrentID(null);
    }

    return (
        <div className={classes.root} >
            <Grid container direction="column" spacing={2}>
            {status === 200 ? (
                events.length ?
                events.map((event) => (
                    <Grid key={event._id} item>
                        <Card className={classes.eventCard}>
                            <CardContent className={classes.content}>
                                <Typography className={classes.contentData} variant="h5">{event.title}</Typography>
                                <Divider orientation="vertical" />
                                <Typography className={classes.contentData} variant="h5">{dayjs(event.beginDate).format('LLL')}</Typography>
                            </CardContent>
                            <CardActions className={classes.cardActions}>
                                <Button variant="contained" disabled ><Visibility/></Button>
                                <Button variant="contained" onClick={() => {setCurrentID(event._id); setViewParts(true);}}><PeopleAlt/></Button>
                                <Button variant="contained" onClick={() => {setCurrentID(event._id); setEditing(true);}}><Edit/></Button>
                                <Button variant="contained" color="secondary" onClick={() => {setCurrentID(event._id); setDeleteOpen(true);}}><Delete/></Button>
                            </CardActions>
                        </Card>
                    </Grid>
                )) :
                (<Typography>No events found</Typography>)
            ) : (
                status === 401 ? (<Typography>Invalid API key</Typography>) : (<Typography>Either still loading or an error, please be patient</Typography>)
            )}
            </Grid>
            <EditMenu api_key={api_key} ID={currentID} open={editing} cancel={cancelEdit} />
            <DeleteDialog open={deleteOpen} cancel={cancelDelete} deleteEvent={confirmDelete} />
            <ViewParticipants api_key={api_key} open={viewParts} ID={currentID} cancel={cancelViewPart}/>
        </div>
    );
}

export default ManageEvents;