import React, { useLayoutEffect, useState } from 'react';
import { Hidden, Drawer, Typography, AppBar, IconButton, Toolbar, CssBaseline, Card, CardContent, TextField, CardActions, Button } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu'

import useStyles from './dash_styles';

import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import DayjsUtils from '@date-io/dayjs';
import MenuDrawer from './components/Dashboard/Drawer/MenuDrawer';
import CreateEvent from './components/Dashboard/CreateEvent/CreateEvent';
import ManageEvents from './components/Dashboard/Management/ManageEvents';
import News from './components/Dashboard/News/News';
import { Redirect, Route, Switch } from 'react-router-dom';
import { InputOutlined } from '@material-ui/icons';
import { useDispatch } from 'react-redux';
import { login } from './actions/events';
import Cookies from 'universal-cookie';
import DefaultLoad from './components/Loading/DefaultLoad';

const Dashboard = () => {
    const classes = useStyles();
    const [ tempKey, setTempKey ] = useState("");
    const [ key, setKey ] = useState("");
    const [ valid, setValid ] = useState(false);
    const [ error, setError ] = useState(false);
    const [ loading, setLoading ] = useState(false);
    const dispatch = useDispatch();

    const [mobileOpen, setMobileOpen] = useState(false);
    const tokenCookie = new Cookies();

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const handleLogin = () => {
        setKey(tempKey);
        setLoading(true);
        dispatch(login(tempKey, callback));
    }

    const callback = (res, key) => {
        setLoading(false);
        if (res.status === 200) {
            setValid(true); 
            setError(false);
            tokenCookie.set('token', key, { maxAge: 259200, sameSite: 'strict', secure: true, path: "/"});
        }
        else { 
            setError(true); 
            setValid(false); 
            tokenCookie.remove('token', { maxAge: 259200, sameSite: 'strict', secure: true, path: "/"});
        }
    }

    useLayoutEffect(() => {
        const t = tokenCookie.get('token');
        if(!valid && t && t !== "")
        {
            setLoading(true);
            setKey(t);
            dispatch(login(t, callback));
        }
    }, []);

    const logout = () => {
        setKey("");
        setValid(false);
        setError(false);
        tokenCookie.remove('token', { maxAge: 259200, sameSite: 'strict', secure: true, path: "/"});
    }

    const container = window !== undefined ? () => document.body : undefined;

    return (
        valid ? (
        <div className={classes.root} >
            <CssBaseline />
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar className={classes.tools}>
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="start"
                    onClick={handleDrawerToggle}
                    className={classes.menuButton}
                >
                    <MenuIcon />
                </IconButton>
                <Typography variant="h6" noWrap>
                    Vilkkoni Control Panel
                </Typography>

                </Toolbar>
            </AppBar>
            <nav className={classes.drawer} >
                <Hidden mdUp implementation="js">
                    <Drawer
                        container={container}
                        variant="temporary"
                        anchor={'left'}
                        open={mobileOpen}
                        onClose={handleDrawerToggle}
                        classes={{
                        paper: classes.drawerPaper,
                        }}
                        ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                        }}
                    >
                    <MenuDrawer logout={logout} />
                    </Drawer>
                </Hidden>
                <Hidden smDown implementation="js">
                    <Drawer
                        classes={{
                        paper: classes.drawerPaper,
                        }}
                        variant="permanent"
                        open
                    >
                    <MenuDrawer logout={logout} />
                    </Drawer>
                </Hidden>
            </nav>
            <main className={classes.content}>
                <div className={classes.toolbar} />
                <MuiPickersUtilsProvider utils={DayjsUtils}>
                    <Switch>
                        <Route exact path="/" >
                            <ManageEvents api_key={key} />
                        </Route>
                        <Route exact path="/create">
                            <CreateEvent api_key={key} />
                        </Route>
                        <Route exact path="/news">
                            <News api_key={key} />
                        </Route>
                        <Route exact path="/statistics">
                            <Typography variant="h5" color="secondary">
                                WORK IN PROGRESS
                            </Typography>
                        </Route>
                        <Route path="*">
                            <Redirect to="/" />
                        </Route>
                    </Switch>
                </MuiPickersUtilsProvider>
            </main>
        </div> 
        ) : ( !loading ? 
            <div className={classes.loginContainer}>
                <Card className={classes.loginCard}>
                    <Typography>
                        Enter credentials
                    </Typography>
                    <CardContent>
                        <TextField variant="outlined" value={tempKey} onChange={(e) => setTempKey(e.target.value)} error={error} />
                    </CardContent>
                    <CardActions>
                        <Button variant="contained" color="primary" onClick={handleLogin} >
                            Log in &nbsp;
                            <InputOutlined />
                        </Button>
                    </CardActions>
                </Card>
            </div>
            :
            <DefaultLoad />
        )
    );
}

export default Dashboard;