import { Button, Grid, TextField, Typography } from '@material-ui/core';
import { DataGrid } from '@material-ui/data-grid';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getNews, postNews } from '../../../actions/news';
import SubmitDialog from './SubmitDialog';

import useStyles from './styles';

const columns = [
    {field: "createdAt", headerName: "Date", flex: 0.2, type: "date"},
    {field: "title", headerName: "Title", flex: 0.5},
    {field: "content", headerName: "Content", flex: 1},
]

const News = ({ api_key }) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const [data, setData] = useState({title: "", content: ""})
    const [submit, setSubmit] = useState(false);
    const news = useSelector((state) => state.news.map((n, i) => n.id ? n : {...n, id: i, createdAt: new Date(n.createdAt)}));

    useEffect(() => {
        dispatch(getNews(api_key))
    }, [dispatch, api_key])

    const handleSubmit = () => {
        if (data.title !== "" && data.content !== "") {
            setSubmit(true);
        }
        console.log(news)
    };

    const handleDialogClose = () => {
        setSubmit(false);
    };

    const handleDialogAccept = () => {
        dispatch(postNews(api_key, data))
        setSubmit(false);
        clearAll();
    };

    const clearAll = () => {
        setData({title: "", content: ""});
    };

    return (
        <div className={classes.newsContainer}>
            <SubmitDialog open={submit} handleCancel={handleDialogClose} handleAccept={handleDialogAccept} />
            <div>
            <Typography className={classes.titles} variant="h5">
                Post a newsletter - &nbsp;
            </Typography>
            <Typography className={classes.titles} variant="h5" color="secondary"> WORK IN PROGRESS</Typography>
            </div>
            <form className={classes.createForm}>
                <Grid container direction="column" spacing={3}>
                    <Grid item><TextField className={classes.inputField} variant="outlined" label="Title" value={data.title} onChange={(d) => setData({...data, title: d.target.value })} /></Grid>
                    <Grid item><TextField className={classes.inputField} variant="outlined" label="Content" multiline rows={7} inputProps={{style: {resize: "both"}}} value={data.content} onChange={(d) => setData({...data, content: d.target.value })} /></Grid>
                    <Grid item container direction="row" alignItems="center" justify="flex-start">
                        <Grid item style={{marginRight: '2rem'}}><Button variant="outlined" color="secondary" onClick={clearAll}>Clear</Button></Grid>
                        <Grid item><Button variant="contained" color="primary"  onClick={handleSubmit}>Submit</Button></Grid>
                    </Grid>
                </Grid>
            </form>
            <Typography variant="h5" style={{marginTop: '2rem'}}>Old Newsletters</Typography>
            <div className={classes.oldData}>
                <DataGrid 
                    columns={columns} 
                    rows={news}
                    disableSelectionOnClick
                    sortModel={[
                        {
                          field: 'createdAt',
                          sort: 'asc',
                        },
                    ]}
                />
            </div>
        </div>
    )
}

export default News;