import {makeStyles} from '@material-ui/core/styles';

export default makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    drawer: {
        [theme.breakpoints.up('md')]: {
          width: '240px',
          flexShrink: 0,
        },
    },
    appBar: {
        [theme.breakpoints.up('md')]: {
            width: `calc(100% - 240px)`,
            marginLeft: '240px',
        },
        zIndex: '200'
    },
    menuButton: {
        marginRight: theme.spacing(2),
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
        width: '240px',
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        height: '100vh',
    },
    languages: {
        color: '#000'
    },
    tools: {
        justifyContent: 'space-between'
    },
    loginCard: {
        justifyContent: 'center',
        display: 'flex',
        flexDirection: 'column',
        alignSelf: 'center',
        padding: '1rem'
    },
    loginContainer: {
        display: 'flex',
        justifyContent: 'center',
        height: '100vh'
    }
}))