import React, { useState } from 'react';
import { FormControlLabel, TextField, Typography, Checkbox, Button, Grid } from '@material-ui/core';
import { DateTimePicker } from '@material-ui/pickers';
import FileBase from 'react-file-base64';

import useStyles from './styles'
import dayjs from 'dayjs';
import PreviewEvent from './PreviewEvent'
import SubmitDialog from './SubmitDialog';
import { useDispatch } from 'react-redux';
import { createEvent } from '../../../actions/events';

const CreateEvent = ({api_key}) => {
    const classes = useStyles();
    const dispatch = useDispatch();

    const [eventData, setEventData] = useState({creator: "Vilkkoni", title: "", message: "", location: "", tags: [""], image: "", maxPartCount: 3, partCount: 0, price: 0, beginDate: dayjs().toDate()})
    const [notify, setNotify] = useState(true);
    const [submit, setSubmit] = useState(false);

    const handleSubmit = () => {
        setSubmit(true);
    };

    const handleDialogClose = () => {
        setSubmit(false);
    };

    const handleDialogAccept = () => {
        dispatch(createEvent(api_key, eventData));
        setSubmit(false);
        clearAll();
    };

    const clearAll = () => {
        setEventData({creator: "Vilkkoni", title: "", message: "", location: "", tags: [""], image: "", maxPartCount: 3, partCount: 0, price: 0,  beginDate: dayjs().toDate()});
    };

    return (
        <div>
            <SubmitDialog open={submit} handleCancel={handleDialogClose} handleAccept={handleDialogAccept} notify={notify} />
            <Typography className={classes.titles} variant="h5">
                Create a new event
            </Typography>
            <form className={classes.createForm}>
                <Grid container direction="column" spacing={3}>
                    <Grid item><TextField className={classes.inputField} variant="outlined" label="Title" value={eventData.title} onChange={(d) => setEventData({...eventData, title: d.target.value })} /></Grid>
                    <Grid item><TextField className={classes.inputField} variant="outlined" label="Content" multiline rows={4} inputProps={{style: {resize: "both"}}} value={eventData.message} onChange={(d) => setEventData({...eventData, message: d.target.value })} /></Grid>
                    <Grid item><TextField className={classes.inputField} variant="outlined" label="Location" value={eventData.location} onChange={(d) => setEventData({...eventData, location: d.target.value})} /></Grid>
                    <Grid item><TextField className={classes.inputField} variant="outlined" label="Search tags, comma-delimited" value={eventData.tags} onChange={(d) => setEventData({...eventData, tags: d.target.value.split(',') })} /></Grid>
                    <Grid item container direction="row" alignItems="center" justify="flex-start">
                        <Grid item><TextField variant="outlined" label="Participant count" type="number" value={eventData.maxPartCount} style={{marginRight: '2rem'}} onChange={(d) => setEventData({...eventData, maxPartCount: d.target.value })} /></Grid>
                        <Grid item><DateTimePicker label="Date and time" value={eventData.beginDate} onChange={(d) => setEventData({...eventData, beginDate: d })} ampm={false}/></Grid>
                    </Grid>
                    <Grid item><div className={classes.fileInput}><Typography>Select image for the event:</Typography><FileBase type="file" multiple={false} onDone={({base64}) => setEventData({...eventData, image: base64})} /></div></Grid>
                    <Grid item><FormControlLabel control={<Checkbox checked={notify} onChange={(s) => {setNotify(!notify)}} />} label="Notify subsribers on this event" /></Grid>
                    <Grid item container direction="row" alignItems="center" justify="flex-start">
                        <Grid item style={{marginRight: '2rem'}}><Button variant="outlined" color="secondary" onClick={clearAll}>Clear</Button></Grid>
                        <Grid item><Button variant="contained" color="primary"  onClick={handleSubmit}>Submit</Button></Grid>
                    </Grid>
                </Grid>
            </form>
            <div style={{marginTop: '3rem'}}>
                <Typography className={classes.titles} variant="h5">
                    Preview
                </Typography>
                <PreviewEvent event={eventData} />
            </div>
        </div>
    )
};

export default CreateEvent;