import React from 'react';
import { Divider, ListItem, List, ListItemIcon, ListItemText } from '@material-ui/core';
import TimelineIcon from '@material-ui/icons/Timeline';
import ViewListIcon from '@material-ui/icons/ViewList';
import CreateIcon from '@material-ui/icons/Create';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import useStyles from './styles';
import { useHistory, useLocation } from 'react-router-dom';
import { MailOutline } from '@material-ui/icons';

const MenuDrawer = ({ logout }) => {
    const classes = useStyles();
    const history = useHistory();
    const location = useLocation();

    const handleLink = (link) => {
        history.push({ pathname: link, search: location.search});
    }

    return (
        <div>
            <div className={classes.toolbar} />
            <Divider />
            <List className={classes.menuList} >
                <ListItem button key="Inspect" onClick={() => {handleLink("/")}}>
                    <ListItemIcon><ViewListIcon /></ListItemIcon>
                    <ListItemText primary="Manage events" />
                </ListItem>
                <ListItem button key="create" onClick={() => {handleLink("/create")}}>
                    <ListItemIcon><CreateIcon/></ListItemIcon>
                    <ListItemText primary="Create event" />
                </ListItem>
                <ListItem button key="news" onClick={() => {handleLink("/news")}}>
                    <ListItemIcon><MailOutline /></ListItemIcon>
                    <ListItemText primary="Newsletter" />
                </ListItem>
                <ListItem button key="stats" onClick={() => {handleLink("/statistics")}}>
                    <ListItemIcon><TimelineIcon /></ListItemIcon>
                    <ListItemText primary="Statistics" />
                </ListItem>
            </List>
            <Divider />
            <List className={classes.menuList} >
                <ListItem button key="logout" onClick={() => logout()} >
                    <ListItemIcon><ExitToAppIcon color="secondary"/></ListItemIcon>
                    <ListItemText primary="Logout" />
                </ListItem>
            </List>
        </div>
    )};

export default MenuDrawer; 